@layer base {
  * {
    @apply m-0 p-0;
  }

  body {
    @apply m-0 antialiased;
    font-family: theme("fontFamily.sans");
    overflow-x: hidden;
  }

  code {
    font-family: theme("fontFamily.mono");
  }
}
